import OrderFormItem from './orderFormItem.vue'
import MyHead from '../../common/myHead.vue'
import {publicing,myGET} from '../../api/api.js'
import {getMyRecive} from '../../api/request.js'
export default {
	components:{OrderFormItem,MyHead},
	data() {
		return {
			tabIndex: 0,
			orderForms:[],
			orderMenuIndex: 0,
			mytext:"我的订单"
		}
	},
	computed: {
		
	},
	onShow() {
		this.loadOrderForm()
	},
	methods: {
		loadOrderForm(){
			console.log("加载我发布的订单了哟")
			let userInfo = uni.getStorageSync("userInfo")
			// 特殊的查询，可以直接指定字段
			let search_data = {
				e_initId: userInfo._id
			}
			console.log(search_data) 
			myGET(getMyRecive,search_data)
			.then((res)=>{
				console.log(res)
				this.orderForms=res.list;
			})
			.catch((err)=>{
				console.log("错误")
			})
		},
		
		async switchTab(index) {
			if(this.tabIndex === index) return
			this.tabIndex = index
			if(this.tabIndex) {
				await this.getOrders()
			}
		},
		handleSwiperItemChange() {	//禁止手动滑动
			return true
		},
		async switchMenuTab(index) {
			if(this.orderMenuIndex === index) return
			this.orderMenuIndex = index
			await this.getOrders()
		},
		async getOrders() {
			if(!this.orderMenuIndex) {
				this.orders = await this.$api('orders')
			} else {
				this.storeOrders = await this.$api('storeOrders')
			}
		}
		
		
		
		
	}
}
